<script>
    import { onMount } from "svelte";
    import { link, navigate } from "svelte-routing";

    let topWaves, botWaves;
    onMount(()=>{
        topWaves = document.querySelector(".top-waves").clientHeight;
        botWaves = document.querySelector(".bot-waves").clientHeight;
    })
    window.addEventListener("resize", () => {
        if(location.pathname == "/"){
            topWaves = document.querySelector(".top-waves").clientHeight;
            botWaves = document.querySelector(".bot-waves").clientHeight;
        }
    })

    
    function createRipple(event, color) {
        const button = event.currentTarget;
        const pos = button.getBoundingClientRect();
        const circle = document.createElement("span");

        circle.style.background = color;
        circle.style.top = (event.clientY - (button.offsetHeight / 2 + pos.top)) - 124 + "px";
        circle.style.left = (event.clientX - (button.offsetWidth / 2 + pos.left)) + "px";
        circle.classList.add("ripple");

        const ripple = button.getElementsByClassName("ripple")[0];

        if (ripple) {
            ripple.remove();
        }

        button.appendChild(circle);
    }

</script>

<svelte:head>
    <title>Nalara Store</title>
    <meta content="#fff" name="theme-color">
</svelte:head>

<div class="full row center-xs bottom-xs">
    <svg style="top:-{topWaves / 2 - 16 }px" class="top-waves" xmlns="http://www.w3.org/2000/svg" viewBox="0 0 1440 320"><path fill="#fff" fill-opacity="1" d="M0,160L48,181.3C96,203,192,245,288,229.3C384,213,480,139,576,106.7C672,75,768,85,864,106.7C960,128,1056,160,1152,192C1248,224,1344,256,1392,272L1440,288L1440,320L1392,320C1344,320,1248,320,1152,320C1056,320,960,320,864,320C768,320,672,320,576,320C480,320,384,320,288,320C192,320,96,320,48,320L0,320Z"></path></svg>
    <div class="box">
        <h1>Nalara Store</h1>
        <img src="/src/img/Stuck at Home Group Call.svg" alt="Stuck at Home Group Call" />
        <button class="row middle-xs center-xs" on:click="{e => {createRipple(e, "rgba(255, 255, 255, 0.7)");navigate("/home")}}">
            Take a Look
        </button>
        <button class="row middle-xs center-xs" on:click="{e => {createRipple(e, "rgba(34, 145, 255, 0.2)");navigate("/signin")}}">
            Sign in
        </button>
        <p>
            Don't have an account? <a href="/signup" use:link>Sign up</a>
        </p>
    </div>
    <svg style="bottom:-{botWaves / 2 - 16}px" class="bot-waves" xmlns="http://www.w3.org/2000/svg" viewBox="0 0 1440 320"><path fill="#fff" fill-opacity="1" d="M0,160L48,181.3C96,203,192,245,288,229.3C384,213,480,139,576,106.7C672,75,768,85,864,106.7C960,128,1056,160,1152,192C1248,224,1344,256,1392,272L1440,288L1440,320L1392,320C1344,320,1248,320,1152,320C1056,320,960,320,864,320C768,320,672,320,576,320C480,320,384,320,288,320C192,320,96,320,48,320L0,320Z"></path></svg>
</div>

